                // 1. Â∑≤ÈÅ∏ÂÆö focus key (ÈùûÈö®Ê©üÊ®°Âºè) Êàñ 2. ÁÇ∫Èö®Ê©üÊ®°Âºè
                // 3. GetLuckyBox Modal ÂøÖÈ†àÂ∑≤ÈóúÈñâ (‰ª•ÂÖçÂú® Modal ‰∏≠Ë™§Ëß∏)
                const isReadyToDraw = (that.luckyDrawFocusKey !== '' || that.luckyDrawIsRandom) &&
