                        title: 'Â≠óÂ§™Â∞èÂóéÔºü',
                        intro: '‰∫∫ÂêçËàáËÅ∑Á®±ÈÉΩÊòØÊñáÂ≠óÂ§ßÂ∞èÈÉΩÂèØ‰ª•Êîπ',
                        beforeAction(Element, TutorialNode, $next) {
                            // that.triggerModal({ key: 'Setting' });
                            setTimeout(() => {
                                document.querySelector('#SettingBox').scroll({ top: 0 });
                                $next();
                            }, 100);
                        },
                    },
