                        intro: 'Â¶ÇÊûú‰Ω†Áé©Â§†‰∫ÜÊàëÂÄëÂëàÁèæÁöÑÁØÑ‰æãÊäΩÁçéÔºåÂèØ‰ª•ÈªûÊìäÈÄôË£°Ê∏ÖÊ•öÊâÄÊúâË≥áÊñôÈáçÊñ∞ÈñãÂßã',
                        beforeAction(Element, TutorialNode, $next) {
                            that.triggerModal({ key: 'Setting' });
                            setTimeout(() => {
