                        title: 'ÈñãÊîæÊÉ≥Ê≥ï',
                        intro: 'Áï∂ÁÑ∂‰Ω†‰πüÂèØ‰ª•‰∏çÁî®ÊäΩ‰∫∫Ôºå‰∏ç‰æ∑ÈôêËÆìÊäΩÁçéÊõ¥Â§öËÆäÂåñÔºå‰∏ÄÂ†¥ÊúâË∂£ÁöÑÊäΩÁçéÊ¥ªÂãïÂ∞±Áî±‰Ω†‰æÜÂª∫Á´ã„ÄÇ',
                        beforeAction(Element, TutorialNode, $next) {
